footer {
    color: #DBEDF3;
    font-family: sans-serif;
  }
  
  .w {
    background-image: url('../assets/tom1.png');
  }
  
  .a {
    background-image: url('../assets/tom2.png');
  }
  
  .s {
    background-image: url('../assets/tom3.png');
  }
  
  .d {
    background-image: url('../assets/tom4.png');
  }
  
  .j {
    background-image: url('../assets/snare.png');
  }
  
  .k {
    background-image: url('../assets/crash.png');
  }
  
  .l {
    background-image: url('../assets/kick.png');
  }
  
  .set {
    margin: 10% auto;
  }
  
  .game-over {
    background-color: red;
    opacity: 0.8;
  }
  
  .pressed {
    animation: press 0.1s ease-in-out;
    
  }
  
  .red {
    color: red;
  }
  
  .drum {
    outline: none;
    border: 10px solid #404B69;
    font-size: 5rem;
    font-family: 'Arvo', cursive;
    line-height: 2;
    font-weight: 900;
    color: #DA0463;
    text-shadow: 3px 0 #DBEDF3;
    border-radius: 15px;
    display: inline-block;
    width: 150px;
    height: 150px;
    text-align: center;
    margin: 10px;
    background-color: white;
  }


  @keyframes press {
    from{ box-shadow: initial; opacity: initial;}
    to{box-shadow:  3px 4px 0 #DBEDF3;
    opacity: 0.5;}
  }

  @media only screen and (max-width: 600px) {
    #title {
      font-size: 3.55rem;
    }
  }